@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap);
* {
  font-family: "Roboto Mono", monospace;
}

body {
  margin: 0;
  padding: 0;
}
.App {
  padding: 20px;
}
nav.navbar {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
  padding: 0px 20px;
  background: #2e2e2e;
  margin: 0;
  color: #eee;
}

.navbar-button {
  justify-content: right;
  background-color: #636363;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.home-button {
  text-align: center;
  font-size: 16px;
  text-decoration: none;
  background-color: #636363;
  color: white;
  align-items: center;
}

.oneProduct {
  border: 2px solid #eee;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 2px 2px 10px 0 #eee;
  margin: 50px 5% 0 5%;
  padding: 20px;
  width: 50%;
  height: 50%;
  font-size: 1.7em;
  text-align: center;
}

.user-details {
  align-items: center;
  border: 2px solid #eee;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 2px 2px 10px 0 #eee;
  margin: 50px 5% 0 5%;
  padding: 20px;
  width: 50%;
  height: 50%;
  font-size: 1.7em;
  text-align: center;
}

.cartScreen {
  border: 2px solid #eee;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 2px 2px 10px 0 #eee;
  margin: 50px 5% 0 5%;
  padding: 20px;
  width: 50%;
  height: 50%;
  font-size: 1.7em;
  text-align: center;
}

.popularProducts {
  text-align: center;
}
.form {
  max-width: 30rem;
  margin: 0 auto;
}
.logo {
  width: 80px;
  height: auto;
}

.rowArticle {
  display: flex;
}

.welcomeMessage {
  text-align: center;
}

article {
  border: 2px solid #eee;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 2px 2px 10px 0 #eee;
  margin: 50px 5% 0 5%;
  padding: 20px;
  width: 40%;
  font-size: 1.7em;
  text-align: center;
}

.productContainer {
  display: flex;
  margin: 10px;
  /* padding: 10px; */
  font-size: 10px;
}

.imgContainer {
  /* width: 00px; */
  margin: 0;
  padding: 75px 0;
  border: 2px solid #eee;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 2px 2px 10px 0 #eee;
  margin: 50px 5% 0 5%;
  padding: 10px;
  width: 40%;
  font-size: 1.7em;
  text-align: center;
  image-rendering: 300dpi;
}

.imgProduct {
  width: 300px;
  height: 300px;
}

.description {
  text-align: center;
  justify-content: center;
}

.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form label {
  margin: 1rem 0;
}
button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  margin: 15px 10px;
  border-radius: 3px;
}
#home-btn {
  color: white;
  text-decoration: none;
}
